/* 垂直轮播 */
    .picScroll-top{ margin:0 auto;  width:210px;  overflow:hidden; position:relative;  border:1px solid #f4f3f3;  padding-bottom: 40px; }
    .picScroll-top .hd{ overflow:hidden;  height:30px; padding:0 10px;  text-align: center; position: relative;}
    .picScroll-top .hd .prev,.picScroll-top .hd .next{ display:inline-block;  width:25px; height:16px;position: absolute;left: 50%;top:-1px;margin-left: -55px ;overflow:hidden;
       cursor:pointer; background:url("../images/sc/T1Z0VbFEdcXXbcIIcR-80-112_02.png") 0 0 no-repeat;}
    .picScroll-top .hd .next{ background-position:0 -30px; margin-left: 20px; }
    .picScroll-top .hd .prevStop{ background-position:-60px -100px;  }
    .picScroll-top .hd .nextStop{ background-position:-60px -140px;  }
    .picScroll-top .hd ul{ display: inline-block; overflow:hidden; zoom:1; margin-top:0; zoom:1; }
    .picScroll-top .hd ul li{ float:left;  width:9px; height:9px; overflow:hidden; margin-right:5px; text-indent:-999px; cursor:pointer; background:url("../images/icoCircle.jpg") 0 -9px no-repeat; }
    .picScroll-top .hd ul li.on{ background-position:0 1px; }
    .picScroll-top .bd{ padding:10px;  height: 375px; overflow: hidden; }
    .picScroll-top .bd ul{ overflow:hidden; zoom:1; }
    .picScroll-top .bd ul li{ text-align:center; zoom:1; margin-bottom: 20px;}
    .picScroll-top .bd ul li .pic{ text-align:center; }
    .picScroll-top .bd ul li .pic img{ width:180px; height:120px; display:block;  padding:2px;}
    .picScroll-top .bd ul li .pic a:hover img{ border-color:#999;  }
    .picScroll-top .bd ul li .title{ line-height:24px; }
